import { NVector3, NVectorUtility, MathUtility, PixelMapGlobe, NGeoJSONFeatureService, EsriFeatureService } from '../../../lib';

import { GeometryCommon } from '../../GeometryCommon/GeometryCommon';
import { WallPaperBase } from '../Base/WallPaperBase';

// const earthNoclouds4k = require('./2_no_clouds_4k.jpg');
// const nightDarkLevel2 = require('./night-dark-level2.jpg');
// const galaxyStarfield = require('./galaxy_starfield.png');
// const fairClouds4k = require('./fair_clouds_4k.png');

import * as THREE from 'three';
export class GlobeWrapperThreeDark extends WallPaperBase {

    envirGeometry: THREE.Geometry;
    envirMat: THREE.MeshPhongMaterial;
    envirMesh: THREE.Mesh;

    geometry: THREE.SphereGeometry;
    mesh: THREE.Mesh;
    meshCloud: THREE.Mesh;

    pixelMapGlobe: PixelMapGlobe;

    vec: NVector3;
    radius: number = 1;
    xSeg: number = 360;
    ySeg: number = 180;

    t: number = 0.0;

    material: THREE.MeshPhongMaterial;
    isDone: boolean = false;

    meshSpot: THREE.Mesh[] = [];
    scene: THREE.Scene;

    mapNight: HTMLImageElement;
    mapDay: HTMLImageElement;
    numBaseMapShfit: number = 0;
    bg: boolean;

    constructor(scene: THREE.Scene, x: number = 0, y: number = 0, z: number = 0, radius: number = 1, xSeg: number = 36, ySeg: number = 18, bg: boolean = true) {
        super();
        this.scene = scene;
        this.vec = new NVector3(x, y, z);
        this.xSeg = xSeg; this.ySeg = ySeg; this.radius = radius;
        this.bg = bg;

        this.InitDataStructure();
        this.ImportImage();

        this.InitGlobe();
        this.InitCloud();
    }
    InitGeometry(): void {
        // // throw new Error('Method not implemented.');
    }
    InitMaterial(): void {
        // // throw new Error('Method not implemented.');
    }
    async ImportData() {
        // let url = 'http://services.arcgis.com/WQ9KVmV6xGGMnCiQ/arcgis/rest/services/earthquakes_over_6_since_1996_time/FeatureServer/0/query?where=1%3D1&objectIds=&time=&geometry=&geometryType=esriGeometryPoint&inSR=&spatialRel=esriSpatialRelIntersects&resultType=none&distance=0.0&units=esriSRUnit_Meter&returnGeodetic=false&outFields=*&returnGeometry=true&multipatchOption=xyFootprint&maxAllowableOffset=&geometryPrecision=&outSR=&datumTransformation=&applyVCSProjection=false&returnIdsOnly=false&returnCountOnly=false&returnExtentOnly=false&returnDistinctValues=false&orderByFields=&groupByFieldsForStatistics=&outStatistics=&having=&resultOffset=&resultRecordCount=&returnZ=false&returnM=false&returnExceededLimitFeatures=true&quantizationParameters=&sqlFormat=none&f=pgeojson&token=';
        let url = 'http://services.arcgis.com/As5CFN3ThbQpy8Ph/ArcGIS/rest/services/NaturalEvents_TEST/FeatureServer/0/query?where=1%3D1&objectIds=&time=&geometry=&geometryType=esriGeometryPoint&inSR=&spatialRel=esriSpatialRelIntersects&resultType=none&distance=0.0&units=esriSRUnit_Meter&returnGeodetic=false&outFields=*&returnGeometry=true&multipatchOption=xyFootprint&maxAllowableOffset=&geometryPrecision=&outSR=&datumTransformation=&applyVCSProjection=false&returnIdsOnly=false&returnCountOnly=false&returnExtentOnly=false&returnDistinctValues=false&orderByFields=&groupByFieldsForStatistics=&outStatistics=&having=&resultOffset=&resultRecordCount=&returnZ=false&returnM=false&returnExceededLimitFeatures=true&quantizationParameters=&sqlFormat=none&f=pjson&token=';
        // tslint:disable-next-line:no-any
        EsriFeatureService.getDataFromFeatureService(url).then((json: any) => {
            if (json) {
                // tslint:disable-next-line:no-any
                let d = new NGeoJSONFeatureService(json); (window as any).featureLayer = d;
                // console.log(d);
                this.InitDataStructure();
                this.ImportImage();
                this.InitGlobe();
                this.InitCloud();

            } else {
                console.log('the URL is wrong');
                // return undefined;
            }
        }).catch();
    }
    ImportImage() {
        /* */
    }
    InitDataStructure() {
        let s = 2;
        this.pixelMapGlobe = new PixelMapGlobe(this.xSeg * s, this.ySeg * s, 1);
    }
    Dummy() {/* */ }
    async InitGlobe() {
        this.geometry = new THREE.SphereGeometry(this.radius, this.xSeg, this.ySeg);
        this.geometry.rotateY(MathUtility.Radians(-180));

        this.mapDay = new Image();
        this.mapDay.src = `njslabSandboxStatic/img/2_no_clouds_4k.jpg`; // earthNoclouds4k;

        this.mapNight = new Image();
        this.mapNight.src = `njslabSandboxStatic/img/night-dark-level2.jpg`; // nightDarkLevel2;

        this.mapNight.onload = () => {
            this.mapNight.crossOrigin = 'anonymous';
            let map = THREE.ImageUtils.loadTexture(
                `njslabSandboxStatic/img/night-dark-level2.jpg`
                // this.mapNight.src
            );

            this.material = new THREE.MeshPhongMaterial({
                color: 0xffffff,
                specular: 0x050505,
                shininess: 50,
                map: map
            });
            this.material.bumpMap = map;
            this.material.bumpScale = 0.01;

            this.mesh = new THREE.Mesh(this.geometry, this.material);

            this.scene.add(this.mesh);
            // }
            this.material.needsUpdate = true;
            this.geometry.uvsNeedUpdate = true;
            // this.geometry.nee = true;
            this.mesh.receiveShadow = true;
            this.mesh.castShadow = true;

            GeometryCommon.listMesh.push(this.mesh);
        };
    }
    async InitCloud() {

        let geo = new THREE.SphereGeometry(1.01, this.xSeg, this.ySeg);
        // this.geometry = new THREE.SphereGeometry(1, this.xSeg, this.ySeg);
        this.geometry.rotateY(MathUtility.Radians(-180));
        // let image = document.createElement('img');
        // var texture = new THREE.Texture(image);
        // image.addEventListener('load', function (e: Event) { texture.needsUpdate = true; });

        // let file = `${location.origin}/njslabSandboxStatic/img/fair_clouds_4k.png`;
        let file = `njslabSandboxStatic/img/fair_clouds_4k.png`;

        const theImg = new Image();
        theImg.src = file;

        theImg.onload = () => {
            theImg.crossOrigin = 'anonymous';
            let map = THREE.ImageUtils.loadTexture(theImg.src);

            let material = new THREE.MeshPhongMaterial({
                map: map,
                side: THREE.DoubleSide,
                opacity: 0.2,
                transparent: true,
                depthWrite: false,
            });
            material.bumpMap = map;
            material.bumpScale = 0.01;

            console.log('working');
            this.meshCloud = new THREE.Mesh(geo, material);

            this.mesh.add(this.meshCloud);

            if (this.bg) { this.InitEnviroment(); }
        };

    }
    async InitEnviroment() {

        let file = `njslabSandboxStatic/img/galaxy_starfield.png`;

        const theImg = new Image();
        theImg.src = file;

        theImg.onload = () => {
            theImg.crossOrigin = 'anonymous';
            let map = THREE.ImageUtils.loadTexture(theImg.src);

            this.envirMat = new THREE.MeshPhongMaterial({
                map: map,
                side: THREE.DoubleSide,
                // opacity: 0.2,
                transparent: true,
                depthWrite: false,
            });
            this.envirMat.bumpMap = map;
            this.envirMat.bumpScale = 0.01;
            this.envirGeometry = new THREE.SphereGeometry(100.01, this.xSeg, this.ySeg);
            this.envirMesh = new THREE.Mesh(this.envirGeometry, this.envirMat);
            // this.envirMesh.castShadow = false;
            // this.envirMesh.receiveShadow = false;

            this.scene.add(this.envirMesh);
            // console.log('working');
        };
    }
    InitPost() {
        this.mesh.position.x = this.vec.x;
        this.mesh.position.y = this.vec.y;
        this.mesh.position.z = this.vec.z;
        this.isDone = true;
    }
    Remove() {
        // GlobeWrapperThreeDark.geo = undefined;
        this.scene.remove(this.mesh);
    }
    ShiftShader() {
        if (this.numBaseMapShfit++ % 2 === 0) {
            this.material.map = THREE.ImageUtils.loadTexture(this.mapDay.src);
        } else {
            this.material.map = THREE.ImageUtils.loadTexture(this.mapNight.src);
        }
        this.material.needsUpdate = true;
    }
    UpdatePost() {
        if (this.mesh && this.meshCloud) {
            // this.mesh.rotation.y += 0.0143;
            this.meshCloud.rotation.y += 0.00035;
            this.t += 0.000001;

            // for (let i = 0; i < this.meshSpot.length; ++i) {
            //     // let scale = 1 + Math.cos(this.t);
            //     // let scaleVec = new THREE.Vector3(scale, scale, scale);
            //     // this.meshSpot[i].scale = scaleVec;
            //     // console.log(this.meshSpot[i]);
            //     if (typeof (this.meshSpot[i].material) === typeof (Array)) {
            //         this.meshSpot[i].material[0].color = Math.random() * 0xffffff;
            //     }
            // }
        }
    }
    ToEarth(long: number, lat: number, elevation: number) {
        return NVectorUtility.GetLineVecProjectedToGlobe(new NVector3(long, lat, this.radius), elevation);
    }
    Dispose() {
        this.geometry.dispose();
        //    this.material.dispose();
        this.scene.remove(this.mesh);
        this.scene.remove(this.meshCloud);

        if (this.bg) {
            this.envirMat.dispose();
            this.envirGeometry.dispose();
            this.scene.remove(this.envirMesh);
        }
    }
}