import * as THREE from 'three';
import { OrbitControls } from 'three-orbitcontrols-ts';
import { MouseEventData, CLICK_TYPE } from '../../../../lib';
import { CanvasThree } from '../CanvasThree';
export enum CAMERA_TYPE {
    PERSPECTIVE = 'PERSPECTIVE',
    ORTHOGRAPHIC = 'ORTHOGRAPHIC',
    STEREO = 'STEREO'
}
export class CameraControllerThree {

    canvas: CanvasThree;
    cameraP: THREE.PerspectiveCamera; // Camera;
    cameraO: THREE.OrthographicCamera; // Camera;
    controls: OrbitControls;

    initX: number = 0;
    initY: number = 0;
    prevX: number = 0;
    prevY: number = 0;

    yRotRate: number;
    xRotRate: number;
    xPanRate: number;
    yPanRate: number;
    zPanRate: number;

    rotateRate: number = 150; // 0.000000001; // 150;
    panRate: number = 50; // 0.000000001; // 400;
    zoomRate: number = 200; // 0.000000001; // 200;

    zoomFactor: number = 1;

    frustumSize: number = 0.001; // 500;

    type: CAMERA_TYPE;
    cameraShift: number = 0;

    width: number;
    height: number;
    ableToMove: boolean = true;

    // tslint:disable-next-line:no-any
    stereoCamera: THREE.StereoCamera;

    constructor(canvas: CanvasThree, width: number, height: number, type: CAMERA_TYPE = CAMERA_TYPE.PERSPECTIVE) {
        this.canvas = canvas;
        this.type = type;
        this.width = width; this.height = height;
        // this.aspect = width / height;

        if (this.type === CAMERA_TYPE.PERSPECTIVE) {
            this.GetPerspective(0, 2.3, 4);
        } else if (this.type === CAMERA_TYPE.ORTHOGRAPHIC) {
            this.GetOrthographic();
        } else if (this.type === CAMERA_TYPE.STEREO) {
            this.GetStereo();
        }
        this.stereoCamera = new THREE.StereoCamera(); // (this.canvas.renderer);
    }
    GetStereo() {

        // console.log(StereoEffect);
        // console.log(DeviceOrientationControls);

    }
    set aspect(aspect: number) {
        if (this.type === CAMERA_TYPE.PERSPECTIVE) {
            this.cameraP.aspect = aspect;
        } else if (this.type === CAMERA_TYPE.ORTHOGRAPHIC) {
            //
        }
    }
    get aspect() {
        return this.width / this.height;
    }
    get camera() {
        if (this.type === CAMERA_TYPE.PERSPECTIVE) {
            return this.cameraP;
        } else if (this.type === CAMERA_TYPE.ORTHOGRAPHIC) {
            return this.cameraO;
        } else {
            return this.cameraP;
        }
    }
    GetPerspective(x: number = 0, y: number = 2, z: number = 2) {
        this.cameraP = new THREE.PerspectiveCamera(45, this.width / this.height, 0.01, 10000);
        this.cameraP.position.set(x, y, z);
        this.InitOrbitController();
    }
    GetOrthographic() {
        this.cameraO = new THREE.OrthographicCamera(this.frustumSize * this.aspect / - 2, this.frustumSize * this.aspect / 2, this.frustumSize / 2, this.frustumSize / - 2, 0.01, 50000);
        this.cameraO.position.set(0, 1, 0);
        this.InitOrbitController();
    }
    InitOrbitController() {

        if (this.type === CAMERA_TYPE.PERSPECTIVE) {
            this.controls = new OrbitControls(this.cameraP, this.canvas.renderer.domElement);
        } else if (this.type === CAMERA_TYPE.ORTHOGRAPHIC) {
            this.controls = new OrbitControls(this.cameraO, this.canvas.renderer.domElement);
        }
        // this.controls.panningMode = THREE.HorizontalPanning; // default is THREE.ScreenSpacePanning
        this.controls.minDistance = 0.01;
        this.controls.maxDistance = 500;
        // this.controls.maxPolarAngle = Math.PI / 2;
        this.controls.enablePan = true;
        this.controls.enableZoom = true;

        this.yRotRate = this.rotateRate / this.width * Math.PI / 90;
        this.xRotRate = this.rotateRate / this.height * Math.PI / 180;
        this.xPanRate = (this.panRate / this.width) * this.zoomFactor;
        this.yPanRate = (this.panRate / this.height) * this.zoomFactor;
        this.zPanRate = this.zoomRate / this.height;
        // this.EnableDamping(true);

        // this.controls.maxPolarAngle = 1.5; // ground
    }
    EnableDamping(v: boolean, factor: number = 0.35) {
        this.controls.enableDamping = v; // an animation loop is required when either damping or auto-rotation are enabled
        this.controls.dampingFactor = factor;
    }
    MouseDown(x: number, y: number) {
        this.initX = this.prevX = x; // - this.offsetX;
        this.initY = this.prevY = y; // - this.offsetY;
    }
    MouseMove(x: number, y: number, shift: boolean = false) {
        if (!this.ableToMove) { return; }

        // console.log(x,y)
        const dx = x - this.prevX;		// Difference since last mouse move
        const dy = y - this.prevY;

        // When shift is being helt down, we pan around else we rotate.
        if (!shift) {
            this.controls.rotateLeft(dx * this.xRotRate);
            this.controls.rotateUp(dy * this.yRotRate);
        } else {
            // this.controls.pan(dx * this.xPanRate, dy * this.yPanRate);

            this.controls.panLeft(dx * this.xPanRate, this.cameraP.matrix);
            this.controls.panUp(dy * this.yPanRate, this.cameraP.matrix);

            // this.camera.transform.position.x += -dx * this.xPanRate;
            // this.camera.transform.position.y += dy * this.yPanRate;
        }
        // this.controls.panUp(0.001, this.camera.matrix);
        this.prevX = x;
        this.prevY = y;
        // this.controls.update();
    }
    MouseWheel(v: number) {
        // console.log(v);
        this.controls.enableZoom = true;
        v *= 0.1;

        const dd = this.cameraP.position.length() * 100;

        this.zoomFactor = dd;

        if (v > 0) {
            this.cameraP.position.x *= 0.9;
            this.cameraP.position.y *= 0.9;
            this.cameraP.position.z *= 0.9;
        } else {
            this.cameraP.position.x *= 1.1;
            this.cameraP.position.y *= 1.1;
            this.cameraP.position.z *= 1.1;
        }

    }
    ShiftCamera() {
        this.cameraShift++;
        if (this.cameraShift === 2) { this.cameraShift = 0; }
        if (this.cameraShift === 0) {
            this.GetPerspective();
        } else if (this.cameraShift === 1) {
            this.GetOrthographic();
        }
    }
    MouseEventController(e: MouseEventData) {
        if (e.CLICK_TYPE === CLICK_TYPE.DOWN) { this.MouseDown(e.x, e.y); }
        if (e.CLICK_TYPE === CLICK_TYPE.DRAG) { this.MouseMove(e.x, e.y, e.pressedShift); }
        if (e.CLICK_TYPE === CLICK_TYPE.WHEEL) { this.MouseWheel(e.wheel); }
    }
}