import * as THREE from 'three';

import { CanvasWrapper } from '..';
import { CameraControllerThree } from './CoreThree/CameraController';

export class CanvasThree {
    hostDiv: HTMLElement;
    camera: CameraControllerThree;
    scene: THREE.Scene;
    renderer: THREE.WebGLRenderer;

    canvasWrapper: CanvasWrapper;

    vrBtn: HTMLElement;
    bg: number = 0x000000;
    isActive: boolean = true;

    constructor(canvasWrapper: CanvasWrapper, hostDiv: HTMLElement) {
        this.canvasWrapper = canvasWrapper;
        this.hostDiv = hostDiv;
        this.Init();
    }
    Init() {
        const width = this.hostDiv.clientWidth;
        const height = this.hostDiv.clientHeight;

        this.renderer = new THREE.WebGLRenderer({ antialias: true, alpha: true });
        this.renderer.setPixelRatio(width / height);

        this.camera = new CameraControllerThree(this, width, height);
        this.scene = new THREE.Scene();

        this.renderer.domElement.id = 'THREE_WALLPAPER';
        this.renderer.domElement.style.position = 'absolute';
        this.renderer.domElement.style.pointerEvents = 'none';

        this.renderer.setSize(width, height);
        this.hostDiv.appendChild(this.renderer.domElement);

        this.RenderLoop(this);
    }
    RenderLoop(canvas: CanvasThree) {
        if (!this.isActive) { return; }
        requestAnimationFrame(() => { this.RenderLoop(this); });
        this.UpdateBinding();

        this.camera.controls.update();
        this.renderer.render(this.scene, this.camera.camera);
    }
    UpdateBinding() {
        // console.log('implementation needed');
    }
    ResizeCanvas(width: number, height: number) {
        this.camera.aspect = width / height;
        this.camera.camera.updateProjectionMatrix(); // updateMatrix(); // updateProjectionMatrix();
        this.renderer.setSize(width, height);
    }
    ResizeEffect(width: number, height: number) {
        // this.camera.effect.setSize(width, height);
    }
    Remove() {
        while (this.scene.children.length) {
            this.scene.remove(this.scene.children[0]);
        }
    }

}