import { MouseEventData, ColorUtility } from '../../../lib';
import { GeometryGlobeBase } from './Base/GeometryGlobeBase';
import { SceneWrapper } from '../..';
import * as THREE from 'three';

export class LineSpikeThreeGlobe extends GeometryGlobeBase {

    sceneWrapper: SceneWrapper;

    public static MouseLeftClick(sceneWrapper: SceneWrapper, e: MouseEventData) {
        const matrix = sceneWrapper.geometryCommon.getMatrixAtScreenCoordinates(e.x, e.y);
        const v1 = new THREE.Vector3(0, 0, 0);
        v1.applyMatrix4(matrix);
        v1.multiplyScalar(1.01);
        console.log(v1);

        const v2 = v1.clone();
        v2.multiplyScalar(Math.random() * 2 + 1.1);

        const col = ColorUtility.GetRandomHex();

        const m = this.LineSpike(v1, v2, col);
        sceneWrapper.scene.add(m);

        const sphere = new THREE.SphereGeometry(0.01);
        const material = new THREE.MeshBasicMaterial({ color: col });
        const mesh = new THREE.Mesh(sphere, material);
        mesh.applyMatrix4(matrix);
        mesh.position.multiplyScalar(1.01);
        sceneWrapper.scene.add(mesh);
    }

    // https://stackoverflow.com/questions/42663182/draw-curved-line-in-three-js-from-vector3-to-vector3-on-surface-of-spheregeometr
    // https://jsfiddle.net/prisoner849/b7tfxg8r/
    static LineSpike(pointStart: THREE.Vector3, pointEnd: THREE.Vector3, color: string) {
        const vertices = new Float32Array([
            pointStart.x, pointStart.y, pointStart.z,
            pointEnd.x, pointEnd.y, pointEnd.z,
        ]);

        const geometry = new THREE.BufferGeometry();
        geometry.setAttribute('position', new THREE.Float32BufferAttribute(vertices, 3));

        return new THREE.Line(geometry, new THREE.LineBasicMaterial({ color: color }));
    }

    constructor(sceneWrapper: SceneWrapper, sx: number, sy: number) {
        super();
        this.sceneWrapper = sceneWrapper;
        const d = new THREE.BoxGeometry(0.1, 0.1, 0.1);
        const material = new THREE.MeshBasicMaterial({ color: 0x00ff00 });
        const mesh = new THREE.Mesh(d, material);

        const matrix = this.sceneWrapper.geometryCommon.getMatrixAtScreenCoordinates(sx, sy);
        mesh.applyMatrix4(matrix);
        this.sceneWrapper.scene.add(mesh);
    }
}
