import * as THREE from 'three';

import { AppThreeEnv } from '..';

import { ActiveCommandController } from './ActiveCommand/ActiveCommandController';
import { GeometryCommon } from './GeometryCommon/GeometryCommon';
import { LightingController } from './Light/LightingController';

import { WallPaperBase } from './Definition/Base/WallPaperBase';

import { GlobeWrapperThreeDark } from './Definition/Globe/GlobeWrapperThreeDark';
// import { GlobeLowPoly } from './Definition/GlobeLowPoly';
// import { GlobePixel } from './Definition/GlobePixel';

export class SceneWrapper {
    app: AppThreeEnv;
    activeCommandController: ActiveCommandController;
    geometryCommon: GeometryCommon;
    lightingController: LightingController;

    scene: THREE.Scene;

    definition: WallPaperBase | any;
    numBasemap = 0;

    constructor(app: AppThreeEnv, baseMap: string = 'map') {
        this.app = app;
        this.activeCommandController = new ActiveCommandController(this);
        this.scene = this.app.graphicsCoreWrapper.canvasWrapper.canvasThree.scene;
        this.app.graphicsCoreWrapper.canvasWrapper.canvasThree.UpdateBinding = () => { this.Update(); };
        this.lightingController = new LightingController(this);
        this.geometryCommon = new GeometryCommon(this.app.graphicsCoreWrapper.canvasWrapper.canvasThree);
        this.Init(baseMap);
    }
    Init(baseMap: string) {
        this.app.graphicsCoreWrapper.canvasWrapper.canvasThree.renderer.shadowMap.enabled = true;

        if (baseMap === 'map') { this.definition = new GlobeWrapperThreeDark(this.scene); return; }
        // if (baseMap === 'low') { this.definition = new GlobeLowPoly(this.scene); return; }
        // if (baseMap === 'pixel') { this.definition = new GlobePixel(this.scene); return; }
    }
    Update() {
        this.definition.Update();
    }
    ShiftBasemap() {
        this.definition.Dispose();
        this.numBasemap++;

        // if (this.numBasemap % 3 === 0) {
        //     this.definition = new GlobeWrapperThreeDark(this.scene); return;
        // } if (this.numBasemap % 3 === 1) {
        //     this.definition = new GlobePixel(this.scene); return;
        // } if (this.numBasemap % 3 === 2) {
        //     this.definition = new GlobeLowPoly(this.scene); return;
        // }

    }
    Dispose() {
        this.lightingController.Dispose();
        this.definition.Dispose();
    }
}