import * as THREE from 'three';
import { SceneWrapper } from '..';
export class LightingController {

    sceneWrapper: SceneWrapper;

    hemiLight: THREE.HemisphereLight;
    dirLight: THREE.DirectionalLight;

    constructor(sceneWrapper: SceneWrapper) {
        this.sceneWrapper = sceneWrapper;
        this.Init();
    }
    Init() {
        // this.scene.add( LightUtility.GetAmbientLight());
        // this.scene.add( LightUtility.GetHemiLight());
        // this.scene.scene.add( LightUtility.GetDirLight());

        this.hemiLight = new THREE.HemisphereLight(0xffffff, 0xffffff, 0.6);
        this.hemiLight.color.setHSL(0.6, 1, 0.6);
        this.hemiLight.groundColor.setHSL(0.095, 1, 0.75);
        this.hemiLight.position.set(0, 50, 0);
        this.sceneWrapper.scene.add(this.hemiLight);

        // const hemiLightHelper = new THREE.HemisphereLightHelper( this.hemiLight, 10 );
        // this.scene.scene.add( hemiLightHelper );

        this.dirLight = new THREE.DirectionalLight(0xffffff, 1);
        this.dirLight.color.setHSL(0.1, 1, 0.95);
        this.dirLight.position.set(- 1, 1.75, 1);
        this.dirLight.position.multiplyScalar(30);

        this.dirLight.castShadow = true;
        this.dirLight.shadow.mapSize.width = 2048 * 2;
        this.dirLight.shadow.mapSize.height = 2048 * 2;

        var d = 50;
        this.dirLight.shadow.camera.left = - d;
        this.dirLight.shadow.camera.right = d;
        this.dirLight.shadow.camera.top = d;
        this.dirLight.shadow.camera.bottom = - d;

        this.dirLight.shadow.bias = - 0.0001;

        this.dirLight.shadow.camera.near = 0.001;
        this.dirLight.shadow.camera.far = 350;

        this.sceneWrapper.scene.add(this.dirLight);
        // this.dirLightHeper = new THREE.DirectionalLightHelper( this.dirLight, 10 );
        // this.scene.scene.add( dirLightHeper );
    }
    Dispose() {
        this.dirLight.visible = false;
        this.hemiLight.visible = false;

        this.sceneWrapper.scene.remove(this.dirLight);
        this.sceneWrapper.scene.remove(this.hemiLight);
    }
}