import { AppThreeEnv } from '..';


import { IndependentPanel } from '../lib';

export class ViewWrapper {
    app: AppThreeEnv;
    independentPanel: IndependentPanel;
    div: HTMLElement;
    constructor(app: AppThreeEnv) {
        this.app = app;
        if (this.app.isVR) {
            this.div = this.app.nUIWrapper.nUI.divHost;
        } else {
            this.independentPanel = new IndependentPanel(this.app.nUIWrapper.nUI.independentPanelWrapper, 100, 100, 450, 350, true);
            this.independentPanel.title = 'Globe Network B ';
            this.independentPanel.btnHam.isHidden = true;
            this.independentPanel.btnMini.isHidden = true;
            this.independentPanel.setTopColorTheme(this.app.nUIWrapper.nUI.themeType);
            this.div = this.independentPanel.bodyDiv.div;

            this.independentPanel.RemovePost = () => this.app.Dispose();
            this.independentPanel.btnResize.ResizePropagation = (width: number, height: number) => { this.app.Resize(width, height); };
        }
        // this.div.style.pointerEvents = 'auto';
    }
}