    RemoveAllMesh() {
        let i = GeometryCommon.list.length;
        while (i--) { this.Remove(GeometryCommon.list[i]); }
        // i = GeometryWrapperBase.list.length;
        // while (i--) { GeometryWrapperBase.list[i].Remove(); }
    }

    getMatrixAtScreenCoordinates(x: number, y: number) {
        let p = this.getPointAtScreenCoordinates(x, y) || this.m3;
        let px0 = this.getPointAtScreenCoordinates(x - 1, y) || p;
        let px1 = this.getPointAtScreenCoordinates(x + 1, y) || p;

        let py0 = this.getPointAtScreenCoordinates(x, y - 1) || p;
        let py1 = this.getPointAtScreenCoordinates(x, y + 1) || p;

        px1.sub(px0);
        py1.sub(py0);

        let normal = new THREE.Vector3().crossVectors(px1, py1);
        normal.normalize();

        let tp = new THREE.Vector3().addVectors(p, normal);
        let matrix = new THREE.Matrix4().lookAt(p, tp, new THREE.Vector3(0, 1, 0));
        matrix.setPosition(p);

        return matrix;
    }
    private getPointAtScreenCoordinates(x: number, y: number) {
        let intersects = this.getIntersectionAtScreenCoordinates(x, y);
        if (intersects) { return intersects.point; }
        return undefined;
    }
    private getIntersectionAtScreenCoordinates(x: number, y: number) {



        const recursiveFlag = false;

        this.m.x = (x / this.canvas.canvasWrapper.graphicsCoreWrapper.canvasWrapper.canvasThree.renderer.domElement.clientWidth) * 2 - 1;
        this.m.y = - (y / this.canvas.canvasWrapper.graphicsCoreWrapper.canvasWrapper.canvasThree.renderer.domElement.clientHeight) * 2 + 1;

        let vector = new THREE.Vector3(this.m.x, this.m.y, 0.5);

        this.raycaster.setFromCamera(vector, this.canvas.camera.camera);

        let intersects = this.raycaster.intersectObjects(GeometryCommon.listMesh, recursiveFlag);

        // const camera = this.canvas.canvasWrapper.graphicsCoreWrapper.canvasWrapper.canvasThree.camera.cameraP;
        // const projector = new THREE.Projector();
        // projector.unprojectVector(vector, camera);
        // let raycaster = new THREE.Raycaster(camera.position, vector.sub(camera.position).normalize());

        // let intersects = raycaster.intersectObjects(GeometryCommon.listMesh);

        if (intersects.length > 0) { return intersects[0]; }
        return undefined;
    }

