import { GraphicsCoreWrapper } from '..';
import { CanvasThree } from './CanvasThree/CanvasThree';

export class CanvasWrapper {
    graphicsCoreWrapper: GraphicsCoreWrapper;
    canvasThree: CanvasThree;

    constructor(graphicsCoreWrapper: GraphicsCoreWrapper) {
        this.graphicsCoreWrapper = graphicsCoreWrapper;

        this.canvasThree = new CanvasThree(this, this.graphicsCoreWrapper.app.divHost);
    }
}