import { NUI } from '../lib';
import { AppThreeEnv } from '..';

export class NUIWrapper {
    nUI: NUI;
    app: AppThreeEnv;
    constructor(app: AppThreeEnv) {
        this.app = app;
        this.nUI = new NUI(app.divHost);
        // this.nUI.BindingStoppropagation = this.StopPropagation.bind(this);
        this.nUI.BindingStoppropagation = (v: boolean) => (this.BindingStoppropagation(v));
        this.nUI.BindingActiveCommandController('appThreeEnv', this.app.sceneWrapper.activeCommandController);
    }
    BindingStoppropagation(v: boolean) {
        // console.log(v);
        // console.log('========================================');
        this.app.stopPropagation = v;
        // console.log(this);
    }
    // tslint:disable-next-line:no-any
    PushUI(app: string, ac: any) {
        this.nUI.BindingActiveCommandController(app, ac);
    }
    Resize(width: number, height: number) {
        this.nUI.Resize(width, height);
    }
}