export abstract class WallPaperBase {

    geometry: THREE.BufferGeometry | THREE.BufferGeometry;
    mesh: THREE.Object3D;
    material: THREE.Material;

    uuid: string;
    isHover: boolean = false;
    isLeave: boolean = false;
    isSelected: boolean = false;
    isDrag: boolean = false;
    isMove: boolean = false;

    hasPopup: boolean = false;

    vec: NVector3 = new NVector3(0, 0, 0);
    scale: NVector3 = new NVector3(1, 1, 1);

    t: number = 0.0;
    tOver: number = 0.0;
    tDown1: number = 0.0;
    tDown2: number = 0.0;
    tUp: number = 0.0;

    constructor() {
        //
    }
    get IsMove() {
        return this.isMove;
    }
    set IsMove(v: boolean) {
        this.isMove = v;
    }
    abstract UpdatePost(): void;
    abstract Dispose(): void;
    Dummy() {/* */ }
    Add() {
        // console.log(AppVisThree.app.canvasThree);
        this.uuid = this.mesh.uuid;
        // tslint:disable-next-line:no-string-literal
        this.mesh['geo'] = this;
        // this.sceneGlobeNetwork.geometryCommon.Add(this);
        // console.log(this);
    }
    Update() {

        this.t += 0.1;
        this.UpdatePost();
    }
    Remove() {
        // this.sceneGlobeNetwork.geometryCommon.Remove(this);
    }
    ShiftShader() {
        //
    }
    // MouseDown(m: MouseEventData) {
    //     //
    // }
    // MouseMove(m: MouseEventData) {
    //     //
    // }
}