import { MouseEventData } from '../../../lib';
import { GeometryGlobeBase } from './Base/GeometryGlobeBase';
import { SceneWrapper } from '../..';
import * as THREE from 'three';

export class RPointThreeGlobe extends GeometryGlobeBase {
    sceneWrapper: SceneWrapper;
    geometry: THREE.BufferGeometry;
    material: THREE.MeshPhongMaterial;
    mesh: THREE.Mesh;

    public static MouseLeftClick(sceneWrapper: SceneWrapper, e: MouseEventData) {
        const p = new RPointThreeGlobe(sceneWrapper, e.x, e.y);
        console.log(p);
    }

    constructor(sceneWrapper: SceneWrapper, sx: number, sy: number) {
        super();
        this.sceneWrapper = sceneWrapper;

        const json = this.sceneWrapper.app.asset.rPoint;

        const vertices = new Float32Array(json['v'].flat());
        const faces = new Uint32Array(json['f'].flat());
        const colors = new Float32Array(json['vc'].flat());

        this.geometry = new THREE.BufferGeometry();
        this.geometry.setAttribute('position', new THREE.BufferAttribute(vertices, 3));
        this.geometry.setIndex(new THREE.BufferAttribute(faces, 1));
        this.geometry.setAttribute('color', new THREE.BufferAttribute(colors, 3));

        // Applying transformations
        const scaleMatrix = new THREE.Matrix4().makeScale(0.03, 0.03, 0.03);
        this.geometry.applyMatrix4(scaleMatrix);

        this.geometry.computeVertexNormals();
        this.geometry.computeBoundingBox();

        this.material = new THREE.MeshPhongMaterial({ color: 0x00ff00, vertexColors: true });

        this.mesh = new THREE.Mesh(this.geometry, this.material);
        this.mesh.castShadow = true;
        this.mesh.receiveShadow = true;

        const matrix = this.sceneWrapper.geometryCommon.getMatrixAtScreenCoordinates(sx, sy);
        this.mesh.applyMatrix4(matrix);

        this.sceneWrapper.scene.add(this.mesh);
    }
}
