import { AppThreeEnv } from '..';

import { MouseInteraction } from './Mouseinteraction/MouseInteraction';

export class IO {
    app: AppThreeEnv;
    mouseInteraction: MouseInteraction;
    constructor(app: AppThreeEnv) {
        this.app = app;

        this.mouseInteraction = new MouseInteraction(this.app);
        if (!this.app.isVR) { this.mouseInteraction.InitNativeMouseEvent(this.app.divHost); }
    }
}